@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;300;400;600;700&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-weight:300}body{font-family:'Titillium Web', sans-serif;background:#120E14;color:#07903A}.main__container{display:flex;align-items:center;justify-content:center;padding:50px;flex-direction:column}.main__container .horizontal-bar{background:#07903A;width:5px;height:100%}.main__container .blink_me{-webkit-animation:blinker 1s linear infinite;animation:blinker 1s linear infinite}@-webkit-keyframes blinker{50%{opacity:0}}@keyframes blinker{50%{opacity:0}}.main__container .name{font-size:1rem}.main__container .white-text{color:#cecece}.main__container .blue-text{color:#7389ee}.main__container .green-text{color:#07903A}.main__container .green-text:hover{color:#1ed461;border-bottom:1px solid #1ed461}.main__container .link-text{text-decoration:none}pre{display:block}.fig{white-space:pre;margin-bottom:15px}nav{padding:12px 0px}nav .nav__ul{text-decoration:none;display:flex;align-items:center;justify-content:center}nav .nav__ul .nav__list{list-style-type:none;text-align:center;margin:0 10px}nav .nav__ul .nav__list:hover{color:#1ed461;border-bottom:1px solid #1ed461}

